<template>
  <v-data-table
    :headers="headers"
    :items="items"
    :items-per-page="10"
    class="elevation-1"
    :sort-by="['date', 'check_in']"
    :sort-desc="[true, true]"
    multi-sort
    :custom-filter="filter_today"
    :search="date_filter"
  >
    <template v-slot:top>
      <!-- <v-text-field v-model="search" label="Search" class="mx-4"></v-text-field> -->
      <v-row justify="end"
        ><v-col cols="2" class="mr-3">
          <v-menu
            :close-on-content-click="false"
            :nudge-right="40"
            transition="scale-transition"
            offset-y
            min-width="100px"
            v-model="menu1"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                prepend-icon="mdi-calendar"
                :rules="requireRule"
                v-model="date_filter"
                dense
                label="Date filter"
                readonly
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker v-model="date_filter" @input="menu1 = false"></v-date-picker>
          </v-menu> </v-col
      ></v-row>
    </template>
    <template v-slot:item.covid_tracking="{ item }">
      <v-chip :color="getColor(item.covid_tracking)" dark>
        {{ item.covid_tracking }}
      </v-chip>
    </template></v-data-table
  >
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      date_filter: "",
      menu1: false,
      search: "",
      sortBy: "date",
      sortDesc: true,
      headers: [
        {
          text: "One Mail",
          value: "one_email",
          sortable: false,
          class: "table_header",
          filterable: false,
        },
        {
          text: "รหัสพนักงาน",
          value: "employee_code",
          sortable: false,
          class: "table_header",
          filterable: false,
        },
        {
          text: "เวลาเข้าพื้นที่",
          value: "check_in",
          sortable: false,
          class: "table_header",
          filterable: false,
        },
        {
          text: "เวลาออกจากพื้นที่",
          value: "check_out",
          sortable: false,
          class: "table_header",
          filterable: false,
        },
        {
          text: "สถานะ covid tracking",
          value: "covid_tracking",
          sortable: false,
          class: "table_header",
          filterable: false,
        },
        {
          text: "วันที่",
          value: "date",
          sortable: false,
          class: "table_header",
          filterable: true,
        },
      ],
      items: [],
    };
  },
  methods: {
    async get_timeattendance() {
      const result = await axios.post(
        "http://203.151.164.229:5007/api/v1/admin/get_timeattendance"
      );
      console.log(result.data.result);
      this.items = result.data.result;
      console.log(this.items);
    },
    getColor(covid_status) {
      if (covid_status == "red") return "red";
      else if (covid_status == "green") return "green";
    },
    filter_today(value, search) {
      if (value === search) {
        return true;
      }
    },
  },
  mounted() {
    this.get_timeattendance();
  },
};
</script>

<style scoped>
.titleText {
  font-size: 30;
}
</style>
